<script>
  import { onMount } from 'svelte';
  import { currentLang } from './lib/i18n';
  import Header from './components/Header.svelte';
  import Footer from './components/Footer.svelte';

  // Import components dynamically
  // This tells Svelte to create separate JavaScript chunks for each page
  const pages = {
    home: () => import('./pages/Home.svelte'),
    about: () => import('./pages/About.svelte'),
    services: () => import('./pages/Services.svelte'),
    contact: () => import('./pages/Contact.svelte'),
    booking: () => import('./pages/Booking.svelte'),
    privacy: () => import('./pages/Privacy.svelte'),
  };

  // Use a state variable to hold the loaded page component
  let LoadedPage = null;
  let currentPage = 'home'; // Default to home
  let pageLoading = true;
  let pageTransition = false;
  let lang = 'hu'; // Local state for current language

  // Subscribe to language changes
  currentLang.subscribe(value => {
    lang = value;
  });

  // Function to load and set the current page component
  async function loadPage(pageName) {
     if (!pages[pageName]) {
       console.error(`Page component for "${pageName}" not found.`);
       // Handle unknown page, maybe navigate to a 404 or home
       pageName = 'home'; // Fallback to home
     }

    // Set loading and transition states
    pageTransition = true; // Start fade out/move transition

    // Use a slight delay to allow transition to be visible before changing page
    setTimeout(async () => {
        try {
            const module = await pages[pageName]();
            LoadedPage = module.default;
            currentPage = pageName;
            window.scrollTo(0, 0); // Scroll to top on new page
            pageLoading = false; // Hide initial loader once first page is loaded

            // Update URL hash AFTER loading the new page component
            if (window.location.hash.slice(1) !== pageName) {
                 window.history.pushState(null, null, `#${pageName}`);
            }

        } catch (error) {
            console.error(`Failed to load page component for "${pageName}":`, error);
            // Handle error loading page component, maybe show an error message
            // For now, fallback to home if a specific page fails to load
            if (pageName !== 'home') {
                loadPage('home');
            } else {
                // If home page itself fails, hide loader and show an error message
                 pageLoading = false;
                 LoadedPage = null; // Or a specific Error component
                 // Potentially display an error message in the template if LoadedPage is null
            }
        } finally {
            // Remove transition class after page component is rendered
             // A small delay might be needed here too, or rely on CSS transition duration
             setTimeout(() => {
                 pageTransition = false;
             }, 100); // Match or slightly exceed the CSS transition duration
        }
    }, 300); // Delay to allow fade out transition
  }


  // Navigate function updated to use loadPage
  function navigate(page) {
      // Prevent navigating to the same page unless it's the initial load trigger
     if (page === currentPage && LoadedPage !== null) {
         // If trying to navigate to the current page *after* it's loaded, do nothing
         return;
     }
     loadPage(page);
  }


  // Check for URL hash/path on initial load and navigate accordingly
  onMount(() => {
    // Don't hide loader immediately, wait for the first page component to load
    // pageLoading remains true here until loadPage completes

    // ALWAYS default to Hungarian language regardless of browser settings
    // Consider letting the user's browser language or a previous preference override this if desired
    currentLang.set('hu');
    localStorage.setItem('zimaAutoLang', 'hu');

    // Handle initial page load with both hash and path support
    const handleInitialRoute = () => {
      const hash = window.location.hash.slice(1);
      const path = window.location.pathname;

      // Map path to pages (adjust as needed based on your server setup)
      const pathToPage = {
        '/': 'home',
        '/booking': 'booking',
        '/about': 'about',
        '/services': 'services',
        '/contact': 'contact',
        '/privacy': 'privacy'
      };

      let targetPage = 'home'; // Default page

      // Prioritize hash if present and valid
      if (hash && pages[hash]) {
        targetPage = hash;
      }
      // Otherwise, check if the path maps to a valid page
      else if (pathToPage[path] && pages[pathToPage[path]]) {
        targetPage = pathToPage[path];
        // Update URL to use hash for consistency for future navigation
         // Use replaceState to avoid creating a new history entry just for the hash change
         window.history.replaceState(null, null, `#${targetPage}`);
      }
      // If neither hash nor path matches a known page, target remains 'home'

      console.log("Initial page determined:", targetPage);
      // Load the initial page component
      loadPage(targetPage);
    };

    handleInitialRoute(); // Execute initial routing

    // Listen for hash changes triggered by browser history (back/forward) or manual hash links
    // Note: navigate() itself updates the hash, which will NOT trigger 'hashchange'
    window.addEventListener('hashchange', () => {
      const newHash = window.location.hash.slice(1);
      // Only navigate if the hash is valid and different from the current page
      if (pages[newHash] && newHash !== currentPage) {
         console.log("Hash change detected, navigating to:", newHash);
         navigate(newHash);
      } else if (!newHash && currentPage !== 'home') {
         // Handle navigating back to the root path (no hash)
         console.log("Navigating back to root, loading home.");
         navigate('home');
      } else if (newHash && !pages[newHash]) {
        console.warn(`Attempted to navigate to unknown hash: #${newHash}`);
        // Optionally redirect to a 404 page or home
        navigate('home'); // Fallback for unknown hash
      }
    });

    // Clean up hashchange listener on component destroy
    return () => {
      window.removeEventListener('hashchange', () => {});
      // popstate listener is implicitly handled by navigate updating history state
    };
  });

  // Save language preference when it changes
  currentLang.subscribe(value => {
    if (typeof localStorage !== 'undefined' && value) {
      localStorage.setItem('zimaAutoLang', value);
    }
  });

  // Optional: Import CookieConsent and DiscountPopup dynamically if they are not immediately needed
  // For example, you could load them after a timeout or user interaction
  // let CookieConsentComponent = null;
  // let DiscountPopupComponent = null;
  // onMount(async () => {
  //    const cookieModule = await import('./components/CookieConsent.svelte');
  //    CookieConsentComponent = cookieModule.default;
  //    // For discount popup, maybe load after a delay
  //    setTimeout(async () => {
  //      const discountModule = await import('./components/DiscountPopup.svelte');
  //      DiscountPopupComponent = discountModule.default;
  //    }, 5000); // Load after 5 seconds
  // });
</script>

<div class="app-container {pageLoading ? 'loading' : ''} {pageTransition ? 'page-transition' : ''}">
  <div class="loader" class:hidden={!pageLoading}>
    <div class="logo-container">
      <img src="images/logo.svg" alt="Zima Auto" />
    </div>
    <div class="spinner"></div>
  </div>

  <Header {navigate} {currentPage} {lang} />

  <main>
    {#if LoadedPage}
      <svelte:component this="{LoadedPage}" {navigate} {lang} />
    {:else if !pageLoading}
      <div class="error-container">
        <h1>Error Loading Page</h1>
        <p>Could not load the requested page. Please try again.</p>
        <button on:click={() => navigate('home')}>Go to Home</button>
      </div>
    {/if}
  </main>

  <Footer {navigate} {lang} />

  <CookieConsent {lang} />
  <DiscountPopup {lang} />

</div>

<style>
  @import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700&display=swap');

  :global(:root) {
    --primary: rgba(0, 186, 229, 1);
    --primary-dark: rgba(0, 150, 190, 1);
    --secondary: #13151a;
    --text: #333333;
    --text-light: #666666;
    --light: #f8f9fa;
    --white: #ffffff;
    --gray: #cccccc;
    --dark-gray: #888888;
    --transition: all 0.3s ease;
    --font-family: 'Raleway', sans-serif;
  }

  :global(*) {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }

  :global(html) {
    scroll-behavior: smooth; /* Note: This can sometimes feel slow on very fast scrolls */
  }

  :global(body) {
    font-family: var(--font-family);
    color: var(--text);
    line-height: 1.6;
    font-size: 16px;
    overflow-x: hidden;
    background-color: var(--white);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .app-container {
    position: relative;
    /* The opacity transition here is for the initial loading screen effect */
    opacity: 1;
    transition: opacity 0.5s ease-in-out;
  }

  /* State for the initial loader screen */
  .app-container.loading {
    /* When loading is true, the loader is visible and covers everything */
    /* No opacity change needed on app-container itself, as loader is fixed on top */
  }

  /* Styles for the page transition effect *between* routes */
  .app-container .page-transition main {
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.3s ease, transform 0.3s ease; /* Ensure transition matches setTimeout */
  }

  /* Initial state for main content */
  main {
    min-height: calc(100vh - var(--header-height, 80px) - var(--footer-height, 60px)); /* Adjust based on Header/Footer heights */
    opacity: 1;
    transform: translateY(0);
    transition: opacity 0.3s ease, transform 0.3s ease; /* Default state */
  }

  /* Loader styles - kept as is */
  .loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    transition: opacity 0.5s ease-in-out, visibility 0.5s ease-in-out;
  }

  .loader.hidden {
    opacity: 0;
    visibility: hidden;
    /* Ensure it doesn't block clicks after fading */
    pointer-events: none;
  }

  .logo-container {
    margin-bottom: 2rem;
  }

  .logo-container img {
    height: 70px; /* Adjust as needed */
    animation: pulse 2s infinite ease-in-out;
  }

  @keyframes pulse {
    0% { transform: scale(0.95); }
    50% { transform: scale(1.05); }
    100% { transform: scale(0.95); }
  }

  .spinner {
    width: 40px;
    height: 40px;
    border: 3px solid rgba(0, 186, 229, 0.2);
    border-top-color: var(--primary);
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }

  @keyframes spin {
    to { transform: rotate(360deg); }
  }

  /* Global styles - kept as is */
  :global(a) {
    text-decoration: none;
    color: inherit;
    transition: color 0.3s ease;
  }

  :global(a:hover, a:focus) {
    color: var(--primary);
  }

  :global(button) {
    cursor: pointer;
    font-family: inherit;
  }

  :global(h1, h2, h3, h4, h5, h6) {
    font-weight: 700;
    line-height: 1.2;
  }

  :global(img) {
    max-width: 100%;
    height: auto;
  }

  :global(section) {
    padding: 4rem 2rem;
  }

  :global(.container) {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem; /* Keep horizontal padding */
  }

  :global(.btn) {
    display: inline-block;
    padding: 0.8rem 1.5rem;
    font-weight: 600;
    border-radius: 4px;
    border: none;
    transition: all 0.3s ease;
    text-transform: uppercase;
    font-size: 0.9rem;
    cursor: pointer;
  }

  :global(.btn-primary) {
    background-color: var(--primary);
    color: white;
  }

  :global(.btn-primary:hover),
  :global(.btn-primary:focus) {
    background-color: var(--primary-dark);
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    outline: none;
  }

  :global(.btn-outline) {
    background-color: transparent;
    border: 2px solid white;
    color: white;
  }

  :global(.btn-outline:hover),
  :global(.btn-outline:focus) {
    background-color: white;
    color: var(--text);
    transform: translateY(-2px);
    outline: none;
  }

  .error-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 70vh; /* Use min-height to ensure it takes space */
    text-align: center;
    padding: 2rem;
  }

  .error-container h1 {
    margin-bottom: 1.5rem;
    color: var(--text);
    font-size: 2.5rem;
  }

  .error-container p {
    margin-bottom: 1.5rem;
    color: var(--text-light);
  }

  .error-container button {
    padding: 0.75rem 1.5rem;
    background-color: var(--primary);
    color: white;
    border: none;
    border-radius: 4px;
    font-weight: 600;
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .error-container button:hover,
  .error-container button:focus {
    background-color: var(--primary-dark);
    transform: translateY(-2px);
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    outline: none;
  }

  /* Responsive styles - kept as is */
  @media screen and (max-width: 768px) {
    :global(section) {
      padding: 3rem 1.5rem;
    }

    :global(.container) {
       padding: 0 1rem; /* Adjust horizontal padding */
    }

    :global(.btn) {
      padding: 0.7rem 1.3rem;
      font-size: 0.85rem;
    }
  }
</style>